/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
body {
  overflow-x: hidden; }

:root {
  --margin-y:20px;
  --padding-x:15px;
  --padding-x-grille:var(--padding-x);
  --padding-x-header:var(--padding-x);
  --padding-x-footer:var(--padding-x); }
  @media (min-width: 576px) {
    :root {
      --padding-x:4%; } }
  @media (min-width: 768px) {
    :root {
      --margin-y:40px; } }
  @media (min-width: 960px) {
    :root {
      --margin-y:55px;
      --padding-x: calc(50vw - 868px/2);
      --padding-x-grille:40px;
      --padding-x-header: 40px;
      --padding-x-footer: 40px; } }
  @media screen and (min-width: 1200px) {
    :root {
      --padding-x-grille: calc(50vw - 1090px/2); } }
  @media (min-width: 1400px) {
    :root {
      --padding-x-header: calc(50vw - 1260px/2);
      --padding-x-footer: calc(50vw - 1312px/2); } }

.alignfull, .bleed-container {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding-left: 0;
  padding-right: 0; }

.container {
  padding: 0 var(--padding-x); }

/* Hauteur mini main*/
.site {
  overflow-x: hidden;
  position: relative; }

.site-main {
  min-height: 100vh; }

/* Grid */
.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.show-for-md,
.show-for-lg {
  display: none; }

@media (min-width: 768px) {
  .show-for-md {
    display: initial; }
  .hide-for-md {
    display: none; } }

@media (min-width: 960px) {
  .show-for-lg {
    display: initial; }
  .hide-for-lg {
    display: none; } }

/* Overlay */
.site-main {
  position: relative; }

.overlay {
  background-color: #2D2F38;
  display: none;
  opacity: 0.8;
  transition: opacity var(--transition); }
  .overlay.toggled {
    display: block;
    height: 100%;
    width: 100%;
    z-index: 5;
    position: absolute;
    top: 0;
    left: 0; }

/* Echelle z-index 
Elements qui ont juste besoin d'être au-dessus de leur voisin : 1
Overlay : 5
Volet questionnaire mobile : 6
Sticky header : 10
Menu mobile : 10
Sous-menu desktop : 10;

*/
:root {
  --padding-lien:7px 12px; }

