  input[type="date"]:focus,
  input[type="month"]:focus,
  input[type="week"]:focus,
  input[type="time"]:focus,
  input[type="datetime"]:focus,
  input[type="datetime-local"]:focus,
  input[type="color"]:focus,
  textarea:focus {
    color: var(--bleu); }

select {
  border: 1px solid var(--gris-decor); }

textarea {
  width: 100%; }

/*--------------------------------------------------------------
## Search form
--------------------------------------------------------------*/
.search-form {
  align-items: stretch;
  justify-content: flex-start; }

.search-form .search-field {
  height: 40px;
  line-height: 40px;
  margin-bottom: 0; }

.search-form .search-submit {
  margin-bottom: 0;
  margin-left: 14px;
  padding: 0;
  height: 40px;
  line-height: 40px;
  text-align: center;
  width: 49px; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%; }

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.wp-caption .wp-caption-text {
  margin: 0.8075em 0; }

.wp-caption-text {
  text-align: center; }

.wp-block-columns.antennes-regionales {
  align-items: center; }
  .wp-block-columns.antennes-regionales ol {
    font-size: calc(22 * 0.8125rem / 18);
    color: var(--bleu);
    font-weight: 700; }
    @media (min-width: 768px) {
      .wp-block-columns.antennes-regionales ol {
        font-size: calc(22 * 1.125rem / 18); } }
    .wp-block-columns.antennes-regionales ol li {
      margin-bottom: 1em; }

.decor-traits {
  position: relative; }
  .decor-traits::before {
    background-image: url("icons/decor-droit.svg");
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: contain;
    content: '';
    position: absolute;
    height: 600px;
    right: -300px;
    width: 545px;
    top: -200px; }
    @media (min-width: 768px) {
      .decor-traits::before {
        top: -120%;
        height: 872px;
        right: -90%;
        width: 823px; } }

.wp-block-image figcaption {
  font-style: italic; }

.wp-block-image.alignwide {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 var(--padding-x-grille); }
  .wp-block-image.alignwide figcaption {
    text-align: right; }

/*Galeries créées avec Meow Gallery*/
.mgl-masonry-container, .mgl-tiles-container, .mgl-justified-container, .mgl-square-container,
.mgl-cascade-container {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 var(--padding-x-grille);
  margin-bottom: var(--margin-y); }
